<template>
  <div class="maintain-wrap ios-s">
    <maintain-header></maintain-header>
    <div class="maintain-main">
      <maintain-service></maintain-service>
    </div>
  </div>
</template>

<script>
import MaintainHeader from './maintain-header'
import MaintainService from './maintain-service'
export default {
  name: 'Maintain',
  components: {
    MaintainHeader,
    MaintainService
  }
}
</script>

<style lang="less" scoped>
  .maintain-wrap{
    margin: 50px 0 60px 0;
    .maintain-main{
      padding-bottom: 100px;
    }
  }
</style>



