<template>
  <div class="maintain-header-wrap">
    <div class="header">
      <h1 class="item">重卡汇-服务中心</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Maintain-Header-wrap'
}
</script>

<style lang="less" scoped>
  .maintain-header-wrap{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 1rem;
    border-bottom: 1px solid #eee;
    background-color: cornflowerblue;
    z-index: 10;
    .header{
      display: flex;
      padding: .5rem;
      .item {
        color: #fff;
        vertical-align: middle;
      }
      h1 {
        flex: 1;
        font-size: 1rem;
        padding: 0;
        margin: 0;
        text-align: center;
        color: #fff;
      }
      .icons {
        margin-top: .3rem;
        i {
          font-size: 1rem;
          vertical-align: top;
        }
        img {
          width: 1.1rem;
          height: 1.1rem;
          border-radius: 50%;
        }
      }
    }
  }
</style>



